<template>
  <div class="main-container">
      首页
     <div id="app">{{message}}</div> 
  </div>
</template>

<script>

  import { changeIndex } from "../vuex/actions";

  import loadingss from "../libs/vue-loading";

  export default {
  	data(){
  		message:"hekko"
  	},
    directives: { loading: loadingss },
    vuex: {
      actions: {
        change: changeIndex
      }
    },

	  ready: function() {
	      this.change(0);
	  }
	
	}
</script>



